import './CardGridList.css';
import React from 'react';
import { Link } from 'react-router-dom';
import { arrayOf, shape } from 'prop-types';
import asyncComponent from '../../modules/Async';
const DoubleFacedCard = asyncComponent(() => import('../../components/DoubleFacedCard'));
const SimpleCard = asyncComponent(() => import('../../components/SimpleCard'));

const COUNT_RERENDER_CARDS = 12;
const DOUBLE_FACED_TYPE = 'double-faced';

class CardGridList extends React.Component {

    state = {
        message: '',
        cards: []
    };

    componentDidMount() {
        this.setState({cards: [
            ...this.state.cards,
            ...this.props.cards.splice(0, 12)
        ]});
        this.container.addEventListener('scroll', this.handleScroll.bind(this.container));
    }

    componentWillUnmount() {
        this.container.addEventListener('scroll', this.handleScroll.bind(this.container));
    }

    handleScroll = () => {
        const windowHeight = "innerHeight" in this
            ? this.innerHeight
            : document.documentElement.offsetHeight;
        const docHeight = Math.max(this.scrollHeight, this.offsetHeight, this.clientHeight);
        const windowBottom = windowHeight + window.pageYOffset;
        if (windowBottom >= docHeight) {
            console.log('bottom reached');
            this.setState({
                message:'bottom reached'
            });
        } else {
            console.log('not at bottom');
            this.setState({
                message:'not at bottom'
            });
        }
    };

    render() {
        console.log('STATE', this.state, this.props);
        return (
            <section className="CardGridList__results" ref={(node) => { this.container = node;}} >
                {!!this.state.cards.length && this.state.cards.map((card, index) => (
                    <div key={card.id} className="CardGridList__result">
                        <Link className="CardGridList__resultName" to={`/cards/${card.id}`}>{card.name}</Link>
                        {card.layout && card.layout !== DOUBLE_FACED_TYPE &&
                            <SimpleCard card={card} needForceCheck={index <= COUNT_RERENDER_CARDS} />
                        }
                        {card.layout && card.layout === DOUBLE_FACED_TYPE &&
                            <DoubleFacedCard card={card} needForceCheck={index <= COUNT_RERENDER_CARDS} />
                        }
                        {!card.layout &&
                            <SimpleCard card={card} needForceCheck={index <= COUNT_RERENDER_CARDS} />
                        }
                    </div>
                ))}
            </section>
        );
    }
}

CardGridList.propTypes = {
    cards: arrayOf(shape({}))
};

CardGridList.defaultProps = {
    cards: []
};

export default CardGridList;



